import { makeStyles } from "@material-ui/core";

// menu classes
const useStyles = makeStyles((theme) => ({
  main: {
    backgroundColor: "#FFFFFF",
    borderBottom: "1px solid #EFEFEF",
    display: "flex",
    height: "55px",
    position: "fixed",
    top: 0,
    left: 0,
    width: "100%",
    zIndex: 1000,
  },
  header: {
    height: "100%",
    width: "100%",
    margin: "0 auto",
  },
  logo: {
    width: "152px",
    minWidth: "152px",
  },
  [theme.breakpoints.up("xs")]: {
    header: {
      maxWidth: "90%",
    },
  },
  [theme.breakpoints.up("sm")]: {
    header: {
      width: "95%",
    },
    logo: {
      marginRight: "34px",
    },
  },
  [theme.breakpoints.up("md")]: {
    main: {
      height: "66px",
    },
    header: {
      width: "90%",
      maxWidth: "1100px",
    },
  },
  [theme.breakpoints.up("lg")]: {
    header: {
      maxWidth: "80%",
    },
  },
  [theme.breakpoints.up("xl")]: {
    header: {
      width: "70%",
    },
  },
}));

export default useStyles;
