import LogoSection from "Components/UI/LogoSection/LogoSection";

// components imports
import ForgotForm from "./ForgotForm";

// styles
import useStyles from "./styles/muiStyles";

const LoginComponent = () => {
  const classes = useStyles();

  return (
    <div className={classes["forgot-body"]}>
      <LogoSection />
      <ForgotForm />
    </div>
  );
};

export default LoginComponent;
