import { Typography } from "@material-ui/core";

// components imports
import ForgotForm from "./ForgotForm";

import Header from "../components/Header";

// assets imports
import characterImg from "./assets/character.svg";

// styles
import useStyles from "./styles/muiStyles";

const LoginComponent = () => {
  const classes = useStyles();

  return (
    <>
      {/* Header */}
      <Header type="focus" />

      {/* Section with text and ForgotForm */}
      <section className={classes["forgot-body"]}>
        <div className="img-block">
          <img src={characterImg} alt="character" />
        </div>
        <div>
          <ForgotForm />
          {false && (
            <div className="text-center my-4">
              <p
                style={{
                  color: "#24272C",
                  fontSize: "18px",
                  fontWeight: 500,
                  lineHeight: "21px",
                }}
              >
                {"Donâ€™t have an account? "}
                <Typography
                  onClick={() => window.open("/sign-up", "_self")}
                  variant="caption"
                  style={{
                    cursor: "pointer",
                    color: "#12B0EE",
                    fontSize: "18px",
                    fontWeight: 500,
                    lineHeight: "21px",
                    textDecoration: "none",
                  }}
                >
                  Sign Up
                </Typography>
              </p>
            </div>
          )}
        </div>
      </section>
    </>
  );
};

export default LoginComponent;
