import { TextField, withStyles } from "@material-ui/core";
import clsx from "clsx";
import { useCallback, useEffect, useState } from "react";
import queryString from "query-string";
import validator from "validator";
import PhoneInput from "react-phone-input-2";

import firebase from "firebase";
import { UIButton } from "Components/exports";
import { useToastMessage } from "contexts";
import classes from "./ForgotPassword.module.css";
import { useInterval } from "hooks/useInterval";
import { resetPasswordNoLogin, validateMobileNumber } from "api/Auth";

import { Form } from "antd";
import { sleep } from "Shared/Utility";
import useLocalStorage from "hooks/useLocalStorage";

const CssTextField = withStyles({
  root: {
    background: "#f2f3f7",
    borderRadius: 5,
    width: "100%",

    "& label.Mui-focused": {
      color: "#000000;",
      opacity: 0.5,
    },
    "& .MuiFilledInput-underline:after": {
      border: "none !important",
    },
    "& .MuiFilledInput-underline:before": {
      border: "none !important",
    },
    "& .MuiFilledInput-root": {
      "background-color": "unset",
      "& fieldset": {
        borderColor: "#000000;",
        opacity: 0.1,
        borderRadius: "5px !important",
      },
      "&:hover fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
      "&.Mui-focused fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
    },
  },
})(TextField);

const PasswordComponent = ({
  form,
  isPasswordError,
  setPasswordError,
  validatePassword,
}) => {
  return (
    <div className="col-10 m-auto mt-3">
      <Form.Item
        rules={[{ required: true, message: "Required!" }]}
        name="NewPassword"
        required
        tooltip="Required"
        className="mb-3"
      >
        <CssTextField
          type="password"
          name="NewPassword"
          required
          label="New Password"
          variant="filled"
          readOnly={false}
          onChange={(e) => validatePassword(e.target.value)}
        />
      </Form.Item>

      <Form.Item
        rules={[{ required: true, message: "Required!" }]}
        name="ConfirmPassword"
        required
        tooltip="Required"
      >
        <CssTextField
          type="password"
          name="ConfirmPassword"
          required
          label="Confirm Password"
          variant="filled"
          readOnly={false}
          onChange={(e) => {
            const values = form.getFieldsValue();

            if (values) {
              if (values.NewPassword !== e.target.value) {
                setPasswordError(
                  "Confirm Password must match with New Password"
                );
              } else {
                setPasswordError("");

                validatePassword(e.target.value);
              }
            }
          }}
        />
      </Form.Item>

      {/* helper text */}
      {isPasswordError && isPasswordError.length > 0 && (
        <div>
          <p className={clsx(classes.HelperText, classes.ErrorText)}>
            {isPasswordError}
          </p>
        </div>
      )}
    </div>
  );
};

const ForgotPassword = ({ mobileNumber, location }) => {
  const [isPasswordError, setPasswordError] = useState("");

  const [isPasswordContent, showPasswordContent] = useState(false);
  const [numberError, setNumberError] = useState("");
  const [isOTPSent, setOTPSent] = useState(false);

  const [, setButtonDisabled] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [country, setCountry] = useLocalStorage("country", null);
  const getCountry = useCallback(async () => {
    try {
      const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
        method: "GET",
      });
      if (visitorReq.ok) {
        const visitor = await visitorReq.json();
        setCountry(visitor?.location?.country?.code.toLowerCase());
      } else {
        setCountry("in");
      }
    } catch (error) {
      console.error(error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setCountry]);

  const [form] = Form.useForm();

  const validatePassword = (value) => {
    if (
      validator.isStrongPassword(value, {
        minLength: 8,
        minLowercase: 1,
        minUppercase: 1,
        minNumbers: 1,
        minSymbols: 1,
      })
    ) {
      return setPasswordError("");
    } else {
      if (value.length < 8) {
        return setPasswordError("Password can't be less than 8 characters");
      } else {
        return setPasswordError(
          "Password must contain a combination of Upper and Lowercase Character, Number and Special Character"
        );
      }
    }
  };

  const {
    setSuccessMessage,
    setErrorMessage,
    disabled,
    setDisabled,
    setInfoMessage,
  } = useToastMessage();

  const [resendOTP, setResendOTP] = useState({
    counter: 0,
    disabled: false,
    startTimer: false,
  });

  // eslint-disable-next-line no-unused-vars
  const [data, setData] = useState({ token: null, userId: "" });

  const resendOTPStyle = {
    display: "flex",
    justifyContent: "flex-end",
    width: "max-content",
    fontSize: "15px",
    opacity: resendOTP.disabled || isPasswordContent ? 0.3 : 0.6,
    pointerEvents: resendOTP.disabled || isPasswordContent ? "none" : "all",
    cursor: resendOTP.disabled || isPasswordContent ? "not-allowed" : "pointer",
  };

  const startTimer = () => {
    const number = form.getFieldValue("number");

    if (number) {
      signInWithNumber(`+${number}`);

      setResendOTP((prevState) => ({
        ...prevState,
        disabled: true,
        counter: 30,
        startTimer: true,
      }));
    }
  };

  const signInWithNumber = async (number) => {
    if (number.length <= 10) return;

    setLoading(true);

    setDisabled();

    try {
      const confirmationResult = await firebase
        .auth()
        .signInWithPhoneNumber(number, window.recaptchaVerifier);

      window.confirmationResult = confirmationResult;

      setOTPSent(true);

      setLoading(false);

      return setInfoMessage(
        "You would get an OTP on your registered mobile number.",
        1000
      );
    } catch (err) {
      setInfoMessage(
        "You would get an OTP on your registered mobile number.",
        1000
      );

      setOTPSent(true);
      return setLoading(false);
    }
  };

  const submitPhoneNumberAuthCode = async (otp, cb) => {
    try {
      setDisabled();

      await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.NONE);
      if (window.confirmationResult) {
        window.confirmationResult
          .confirm(otp)
          .then((result) => {
            // console.log(result);
            const user = result;
            const token = user.user._lat;

            const userId = firebase.auth().currentUser.uid;

            firebase.auth().signOut();

            return cb && cb({ userId, token });
          })
          .catch((error) => {
            setLoading(false);
            return setErrorMessage("Invalid OTP");
          });
      } else {
        setLoading(false);

        return setErrorMessage("Invalid OTP");
      }
    } catch (err) {
      setLoading(false);

      return setErrorMessage("Invalid OTP");
    }
  };

  const authCaptchaVerifier = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "captcha-container",
      {
        size: "invisible",
        callback: function (response) {
          const number = form.getFieldValue("number");

          number && signInWithNumber(`+${number}`);
        },
        "expired-callback": function () {
          // Response expired. Ask user to solve reCAPTCHA again.
          // ...
          alert("try again!");
        },
      }
    );
  };

  useEffect(() => {
    authCaptchaVerifier(`+${mobileNumber}`);

    // startTimer();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useInterval(
    () => {
      setResendOTP((prevState) => ({
        ...prevState,
        disabled: prevState.counter > 0 ? true : false,
        counter: prevState.counter - 1,
        startTimer: prevState.counter > 0 ? true : false,
      }));
    },
    resendOTP.counter >= 0 && resendOTP.startTimer ? 1000 : null
  );

  const onFormSubmit = async (values) => {
    if (!isOTPSent && !isPasswordContent) {
      if (values.number && values.number.length >= 10) {
        setLoading(true);

        const res = await validateMobileNumber({
          mobile_number: `+${values.number}`,
        });

        if (!res.success) {
          setLoading(false);
          setOTPSent(true);
          return setErrorMessage(res.message || res.messages.errors[0].message);
        }
        setNumberError("");
        return signInWithNumber(`+${values.number}`);
      }
    }

    if (isOTPSent && !isPasswordContent) {
      if (values.otp.length > 5) {
        setLoading(true);

        submitPhoneNumberAuthCode(values.otp, async ({ userId, token }) => {
          try {
            setData({ userId: userId, token: token });

            setLoading(false);

            showPasswordContent(true);
            return setSuccessMessage("OTP verified successfully!");
          } catch (err) {
            setLoading(false);
            return setErrorMessage(err.message);
          }
        });
      } else {
        return alert("6 character required!");
      }
    }

    if (isOTPSent && isPasswordContent) {
      if (values.ConfirmPassword.length <= 8) {
        return setErrorMessage(
          "Password length should be greater than 8 character."
        );
      }

      setLoading(true);

      const res = await resetPasswordNoLogin({
        mobile_number: `+${values.number}`,
        password: values.NewPassword,
        confirm_password: values.ConfirmPassword,
      });
      if (!res.success) {
        setLoading(false);
        return setErrorMessage(res.messages?.errors[0]?.message || res.message);
      }

      setSuccessMessage("You have successfully changed your password!");

      await sleep(755);

      setInfoMessage("Wait, We are redirecting you to login page!");

      window.open("/login", "_self");

      return setLoading(false);
    }
  };

  useEffect(() => {
    const query = queryString.parse(location?.search);

    if (query.number) {
      if (!query.number >= 10) setButtonDisabled(true);
    } else setButtonDisabled(true);
  }, [location]);

  useEffect(() => {
    if (localStorage.getItem("token")) {
      window.open("/dashboard", "_self");
    }
  }, []);

  useEffect(() => {
    country === null && getCountry();
  }, [country, getCountry]);

  return (
    <section className={classes.Container}>
      <div className={clsx("col-10  m-auto py-4", classes.Form_Title)}>
        <h1>Forgot Password</h1>
      </div>
      <Form
        form={form}
        className={clsx(classes.Form)}
        onFinish={async (values) => onFormSubmit(values)}
      >
        {/* number */}
        <div className="col-10 m-auto mb-3">
          <Form.Item
            rules={[{ required: true, message: "Required!" }]}
            name="number"
            required
            tooltip="Mobile Number Required"
          >
            <PhoneInput
              country={country}
              enableAreaCodes={true}
              onInput={async (e) => {
                if (e.target.value.length >= 10) {
                  e.target.value = e.target.value.slice(0, 10);
                  setButtonDisabled(false);
                } else {
                  setNumberError("");
                  setButtonDisabled(true);
                }
              }}
              label="Mobile No."
              name="number"
              InputProps={{ variant: "filled" }}
              inputProps={{
                disabled: isOTPSent,
                readOnly: isOTPSent,
              }}
            />
          </Form.Item>

          {/* {!isLoading && !isButtonDisabled && (
            <div className="d-flex justify-content-end mt-1">
              <p
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  width: "90%",
                  opacity: resendOTP.disabled ? 0.3 : 0.6,
                  pointerEvents: resendOTP.disabled ? "none" : "all",
                  cursor: resendOTP.disabled ? "not-allowed" : "pointer",
                }}
              >
                {numberError}
              </p>
            </div>
          )} */}
        </div>

        {/* OTP */}
        <div className="col-10 m-auto">
          <Form.Item
            rules={[{ required: isOTPSent, message: "Required!" }]}
            name="otp"
            required={isOTPSent}
            tooltip="OTP Required"
          >
            <CssTextField
              required={isOTPSent}
              type="password"
              name="otp"
              label="Enter OTP"
              variant="filled"
              disabled={isPasswordContent || !isOTPSent}
              readOnly={isPasswordContent}
            />
          </Form.Item>

          {/* helper text */}
          {isOTPSent && (
            <div className="d-flex justify-content-between mt-1">
              <p className={classes.HelperText}>
                sent on: +{form.getFieldValue("number")}
              </p>
              <p
                style={resendOTPStyle}
                onClick={() => {
                  startTimer();
                }}
              >
                Resend OTP
                {resendOTP.counter < 30 &&
                  resendOTP.counter > 0 &&
                  ` in ${resendOTP.counter}s`}
              </p>
            </div>
          )}
        </div>

        {isPasswordContent && isOTPSent && numberError.length < 1 && (
          <PasswordComponent
            form={form}
            setPasswordError={setPasswordError}
            isPasswordError={isPasswordError}
            validatePassword={validatePassword}
          />
        )}
        <div className="col-10 m-auto">
          <UIButton
            className="mt-4 mb-5 mx-0"
            disabled={
              disabled ||
              (isPasswordError && isPasswordError.length > 0) ||
              isLoading ||
              numberError.length > 0
            }
            type="submit"
            svgStyle={{ marginLeft: "10px" }}
          >
            {isOTPSent && !isPasswordContent
              ? "Continue"
              : isPasswordContent
              ? "Change Password"
              : "Get OTP"}
          </UIButton>
        </div>
      </Form>
      <div id="captcha-container"></div>
    </section>
  );
};

export default ForgotPassword;
