/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useRef, useState } from "react";

// mui grid
import { Box, useMediaQuery } from "@material-ui/core";

// @material-ui/styles
import { useTheme } from "@material-ui/core/styles";

import { useOnClickOutside } from "../../../../hooks/useOnClickOutside";

// assets
import ESLogo from "../assets/logo/top-logo1.svg";

// styles
import useStyles from "./styles/muiStyles";

const Header = ({ type }) => {
  const ref = useRef();

  const classes = useStyles();

  // theme setup
  const theme = useTheme();

  // media query breakpoints
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  const [, setMobileNav] = useState(false);

  useOnClickOutside(ref, () => {
    const openedSubMenu = document.querySelector(
        ".has-sub-menu > .sub-menu.open"
      ),
      openedSubMenuLink = document.querySelector(".has-sub-menu > a.open");
    openedSubMenuLink && openedSubMenuLink.classList.remove("open");
    openedSubMenu && openedSubMenu.classList.remove("open");
  });

  useEffect(() => {
    if (typeof window !== "undefined") {
      if (isMobile) {
        const mobileMenuLinks = document.querySelectorAll(
          ".mobile-nav  > ul > li:not(.has-sub-menu) > a, .mobile-nav  > ul > li .sub-menu > ul > li > a"
        );

        // for all subMenuLinks add a click event listener
        mobileMenuLinks.forEach((mobileMenuLink) =>
          mobileMenuLink.addEventListener("click", () => setMobileNav(false))
        );
      }
      const subMenuItems = document.querySelectorAll(".has-sub-menu > a");

      subMenuItems.forEach((subMenuItem) => {
        const subMenuElement = subMenuItem.nextSibling,
          subMenuLinks = subMenuElement.querySelectorAll("li > a");

        // for all subMenuLinks add a click event listener
        subMenuLinks.forEach((subMenuLink) => {
          // close current parent submenu on click
          subMenuLink.addEventListener("click", () =>
            subMenuLink
              .closest(".has-sub-menu")
              .querySelector(".sub-menu")
              .classList.remove("open")
          );
        });

        // for all subMenuItems add a click event listener
        subMenuItem.addEventListener("click", () => {
          const openedSubMenu = document.querySelector(
              ".has-sub-menu > .sub-menu.open"
            ),
            openedSubMenuLink = document.querySelector(
              ".has-sub-menu > a.open"
            ),
            subMenuElement = subMenuItem.nextSibling;

          // check if opened submenu is not current submenu
          if (openedSubMenu && openedSubMenu !== subMenuElement) {
            // close other opened submenu
            openedSubMenuLink.classList.remove("open");
            openedSubMenu.classList.remove("open");
          }

          if (subMenuElement.classList.contains("open")) {
            subMenuItem.classList.remove("open");
            subMenuElement.classList.remove("open");
          } else {
            subMenuItem.classList.add("open");
            subMenuElement.classList.add("open");
          }
        });
      });
    }
  }, [isMobile]);

  return (
    <>
      <div className={classes.main}>
        <header className={classes.header}>
          <Box sx={{ display: "flex", alignItems: "center", height: "100%" }}>
            <a href="/" style={{ display: "inline-flex" }}>
              <img src={ESLogo} alt="Logo" className={classes.logo} />
            </a>
          </Box>
        </header>
      </div>
    </>
  );
};

export default Header;
